$navbarHeight:4.5rem;

body {
  background-color: rgb(246, 251, 255);
  // margin-top: $navbarHeight;
}

@font-face {
  font-family: 'Averta';
  src: local('Averta'), url(./fonts/averta/AvertaStd-Regular.ttf) format('truetype');
}

@font-face {
  font-family: 'Averta-SemiBold';
  src: local('Averta'), url(./fonts/averta/AvertaStd-Semibold.ttf) format('truetype');
}

@font-face {
  font-family: 'Averta-Bold';
  src: local('Averta'), url(./fonts/averta/AvertaStd-Bold.ttf) format('truetype');
}

.header-container {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 3.2fr 0.2fr 0.7fr;
  grid-template-rows: 0.8fr 2.6fr 0.8fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    ". . . ."
    ". header header ."
    ". . . .";
  margin-top: 2.5rem;
}

.header {
  display: grid;
  grid-template-columns: 2.1fr 0.4fr 0.5fr;
  grid-template-rows: 1.3fr 0.4fr 0.9fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    "title title ."
    ". . ."
    "subtitle subtitle .";
  grid-area: header;
}

.projects-container {
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr 3.2fr 0.2fr 0.7fr;
  grid-template-rows: 0.8fr 2.6fr 0.8fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    ". . . ."
    ". projects . ."
    ". . . .";
  margin-top: 2.5rem;
}

.projects {
  display: grid;
  grid-template-columns: 2.1fr 0.4fr 0.5fr;
  grid-template-rows: 1.3fr 0.4fr 0.9fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    "title title ."
    ". . ."
    "subtitle subtitle .";
  grid-area: projects;
}

.title {
  grid-area: title;
}

.subtitle {
  grid-area: subtitle;
}


.title-style {
  font-size: 4rem;
  font-weight: 500;
  font-family: Averta-Bold;

}

.title-description-style{
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
  // text-align: justify;
  color: #494949;
  font-family: Averta-SemiBold;
}

.subtitle-style{
  line-height: 1.75rem;
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
  // text-align: justify;
  font-family: Averta-SemiBold;
  color: #494949;
}